.header {
    width: 100%;
    height: 3rem;
    padding: .5rem;
    color: white;
}

.headerGrid {
    display: grid;
    grid-template-columns: 60% 40%;
}

.card {
    width: 100%;
    height: 2rem;
}

.dp {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    float: left;
    margin-right: 1rem;
}

.dp img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.card small {
    margin-top: 30px;
    margin-right: 1rem;
}



.hcard {
    width: 100%;
    height: 8rem;
    background: white;
    box-shadow: 0px 1px 2px -1px rgba(0, 0, 0, 0.02);
    border-radius: 5px;
    padding: 1.5rem;
    margin-bottom: 1rem;
}

.hlgrid {
    display: grid;
    grid-template-columns: 30% 70%;
}

.icons {
    width: 100%;
    height: 8rem;
    padding: 1rem .5rem;
}

.ccircle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: .2rem;
    margin: 0 auto;
    text-align: center;
}

.circle {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    padding: 1rem .5rem;
    text-align: center;
    font-size: 20px;
}

.ccircle i {
    font-size: 25px;
    text-align: center;
}

.text {
    width: 100%;
    height: 8rem;
    padding: 1rem;
}

.square {
    width: 80%;
    height: 3rem;
    border-radius: 5px;
    padding: .5rem;
    margin: 0 auto;
}

.task {
    width: 100%;
    height: auto;
    display: block;
    padding: .5rem;
}

.text p {
    font-size: 13px;
    color: black;
}

.steps {
    width: 100%;
    height: 3rem;
    background: whitesmoke;
    border-radius: 50px;
    overflow: hidden;
    margin-bottom: 1rem;
}

.stepGrid {
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
}

.step {
    width: 100%;
    height: 3rem;
    padding-top: 1rem;
    text-align: center;
}

.active {
    background: #640ad2;
    color: white;
}

.stacks {
    width: 100%;
    height: auto;
    display: block;
    overflow: hidden;
    margin-bottom: 1rem;
}

.stack {
    text-align: center;
}

.circle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 5px;
    text-align: center;
    background: #000;
    color: white;
    margin: 0 auto;
    margin-bottom: 10px;
    margin-top: 10px;
}

.blue {
    background: linear-gradient(180deg, rgba(47, 124, 178, 1), rgba(10, 93, 148, 1));
}

.magenta {
    background: linear-gradient(180deg, rgba(219, 38, 195, 1), rgba(166, 18, 146, 1));
}

.gold {
    background: linear-gradient(180deg, rgba(234, 136, 8, 1), rgba(179, 105, 9, 1));
}

.green {
    background: linear-gradient(180deg, rgba(97, 176, 67, 1), rgba(58, 148, 22, 1));
}

.crimson {
    background: linear-gradient(180deg, rgba(221, 45, 68, 1), rgba(179, 22, 43, 1));
}




/* phone-only */
@media (max-width: 599px) {
    .header {
        height: auto;
    }

    .headerGrid {
        display: grid;
        grid-template-columns: 100%;
    }

    .btn {
        font-size: 8px;
        float: left;
    }

    .stepGrid {
        grid-template-columns: 100%;
    }

    .steps {
        width: 100%;
        height: auto;
    }

    .btn {
        font-size: 8px;

    }

    .pageTitle {
        float: right;
    }

}

/* .tablet-portrait-up  */
@media (min-width: 600px) {
    .header {
        height: auto;
    }

    .headerGrid {
        display: grid;
        grid-template-columns: 100%;
    }

    .btn {
        font-size: 8px;
    }
}

/* tablet-portait-only */
@media (min-width: 600px) and (max-width: 899px) {
    .headerGrid {
        grid-template-columns: 100%;
    }

    .stepGrid {
        grid-template-columns: 50% 50%;
    }

    .steps {
        width: 100%;
        height: auto;
    }
}

/* tablet-landscape-up */
@media (min-width: 900px) {

    .headerGrid {
        grid-template-columns: 50% 50%;
    }

    .stepGrid {
        grid-template-columns: 50% 50%;
    }

    .steps {
        width: 100%;
        height: auto;
    }
}

/* tablet-landscape-only */
@media (min-width: 900px) and (max-width: 1199px) {

    .headerGrid {
        grid-template-columns: 50% 50%;
    }

    .hcard {
        padding: .5rem;
        height: auto;

    }

    .icons {
        height: auto;
    }

    .icons svg {
        font-size: 15px;
    }

    .text {
        padding: .5rem;
        font-size: 13px;
    }

    .stepGrid {
        grid-template-columns: 50% 50%;
    }

    .steps {
        width: 100%;
        height: auto;
    }
}

@media (min-width: 1025px) and (max-width: 1280px) {

    .headerGrid {
        grid-template-columns: 50% 50%;
    }

    .stepGrid {
        grid-template-columns: 20% 20% 20% 20% 20%;
    }

    .steps {
        width: 100%;
        height: auto;
    }

    .steps a {
        font-size: 11px;
    }
}

