    }

    .steps a {
        font-size: 11px;
    }
}

/* desktop-only */
@media (min-width: 1300px) and (max-width: 1799px) {

    .headerGrid {
        grid-template-columns: 50% 50%;
    }

    .stepGrid {
        grid-template-columns: 20% 20% 20% 20% 20%;
    }

    .steps {
        width: 100%;
        height: auto;
    }

    .steps a {
        font-size: 13px;
    }
}

/* big-desktop-up */
@media (min-width: 1800px) {

    .headerGrid {
        grid-template-columns: 50% 50%;
    }

    .stepGrid {
        grid-template-columns: 20% 20% 20% 20% 20%;
    }

    .steps {
        width: 100%;
