.icon {
    width: 100%;
    height: 3rem;
    display: none;
    padding: .5rem;
}

.closeicon {
    width: 100%;
    height: 2rem;
    display: none;
}

.navigation {
    width: 13%;
    height: 100vh;
    padding: 0;
    display: block;
    /* background: #640ad2; */
    background: white;
    /* color: white; */
    position: fixed;
    float: left;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.logo {
    width: 100%;
    height: 5rem;
    padding: .5rem;
    text-align: center;
    overflow: hidden;
    margin-bottom: 2rem;
}

.logo img {
    width: 30%;
}

.logo small {
    display: block;
    font-size: 10px;
}

.profile {
    width: 100%;
    height: 10rem;
    padding: .5rem;
    text-align: center;
    overflow: hidden;

}

.photo {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: white;
    margin: .5rem;
    margin: 0 auto;
    overflow: hidden;
}

.photo img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.url {
    width: 100%;
    height: auto;
    display: block;
}

.url ul {
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
}

.url ul li a {
    text-decoration: none;
    color: gray;
    font-size: 13px;
}

.url ul li {
    width: 100%;
    padding: 0;
    margin: 0;
    line-height: 3rem;
    display: block;
    padding-left: 1rem;
}

.url ul li a svg {
    margin-right: .5rem;
    margin-top: 1rem;
    float: left;
}

.urlActive {
    width: 100%;
    height: auto;
    display: block;
    background: #640ad2;
    display: none;
}

.open {
    display: block;
}

.url .urlActive a {
    color: white;
    font-size: 12px;
}

.navbar {
    width: 100%;
    height: 3rem;
    padding: .5rem;
    border-bottom: 1px solid whitesmoke;
    overflow: hidden;
}

.title {
    width: 80%;
    height: auto;
    display: block;
    float: left;
}

.name {
    width: 20%;
    height: auto;
    display: block;
    float: left;
}



/* phone-only */
@media (max-width: 599px) {
    .icon {
        display: block;
        position: absolute;
        z-index: 9999;
        color: white;
        float: left;
    }

    .icon svg {
        font-size: 1.3rem;
    }


    .navigation {
        display: none;
    }

    .mobilenavigation {
        /* display: none; */
        position: absolute;
        z-index: 9999;
        width: 100%;
        background: white;
        height: 100vh;
        padding: .5rem;
    }

    .closeicon {
        display: block;
    }

    .logo {
        width: 100%;
        height: 4rem;
        padding: 0;
        text-align: center;
        overflow: hidden;
        margin-bottom: 2rem;
    }

    .logo img {
        width: 10%;
    }



    .title {
        width: 70%;
        padding-left: 2.5rem;
    }

    .name {
        width: 30%;

    }

    .name h6 {
        font-size: 10px;
    }
}

