.app {
    width: 100%;
    height: 100vh;
    background: linear-gradient(0deg, rgba(100, 10, 210, 0.8), rgba(100, 10, 210, 0.8)), url(../assets/tech.jpg);
    padding: 10rem
}

.login {
    width: 40%;
    height: auto;
    border-radius: 20px;
    margin: 0 auto;
    color: white;
    padding: 0;
}

.loginCard {
    width: 100%;
    height: auto;
    padding: 1rem;
    display: block;
    text-align: center;

}


.formPadding {
    padding: .5rem;
}

.logo {
    width: 100%;
    height: 5rem;
    text-align: center;
    overflow: hidden;
}

.logo img {
    width: 10%;
}


.forms {
    width: 100%;
    padding: .5rem 3rem;
}

.forms input[type="email"],
[type="password"],
[type="text"],
[type="date"],
[type="number"],
[type="tel"] {
    width: 100%;
    border-radius: 20px;
    padding: 1rem;
    background: white;
    color: black;
    border: none;
    outline: none;
}



.forms select {
    width: 100%;
    border-radius: 20px;
    padding: 1rem;
    background: white;
}

.forms input[type="submit"] {
    width: 100%;
    border-radius: 0;
    padding: 1rem;
    border-radius: 20px;
    cursor: pointer;
}

.forms input::placeholder {
    font-size: 13px;

}

.warning {
    background: #FFC107;
    color: black;
}


.mainApp {
    width: 100%;
    height: auto;
    display: block;
}

.mainContents {
    width: 87%;
    height: auto;
    display: block;
    float: left;
    margin-left: 13%;
}

.overlay {
    width: 100%;
    height: auto;
    padding: 1rem;
    margin: 0;
    background: linear-gradient(90deg, rgba(130, 29, 197, 0.7), rgba(100, 10, 210, 0.8)), url("../assets/tech.jpg");
    background-size: cover;
}

.cardGrid {
    display: grid;
    grid-template-columns: 24% 24% 24% 24%;
    grid-gap: 1%;
}

.blue {
    background: linear-gradient(180deg, rgba(47, 124, 178, 1), rgba(10, 93, 148, 1));
}

.magenta {
    background: linear-gradient(180deg, rgba(219, 38, 195, 1), rgba(166, 18, 146, 1));
}

.gold {
    background: linear-gradient(180deg, rgba(234, 136, 8, 1), rgba(179, 105, 9, 1));
}

.green {
    background: linear-gradient(180deg, rgba(97, 176, 67, 1), rgba(58, 148, 22, 1));
}

.crimson {
    background: linear-gradient(180deg, rgba(221, 45, 68, 1), rgba(179, 22, 43, 1));
}


.purple {
    background: #F2EBFB;
    color: #713BDB;
}

.cyan {
    background: #EEF9FF;
    color: #49B8FF;
}

.gold {
    background: #FEF3EF;
    color: #FE8A57;
}

.red {
    background: #FFEFF2;
    color: #FD5181;
}

.green {
    color: #57B952;
    background: #EFF9F3;
}

.blue {
    color: #3E4095;
    background: #E9F6FD;
}

.contentSm {
    width: 100%;
    height: auto;
    display: block;
    padding: .5rem;
}

.chartGrid {
    display: grid;
    grid-template-columns: 70% 30%;
}

.box {
    width: 100%;
    height: 40rem;
}

.scroll {
    overflow: scroll;
}

.profileGrid {
    display: grid;
    grid-template-columns: 20% 60% 20%;
}

.profilePix {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto;
}

.profilePix img {
    width: 200px;
    height: 200px;
    border-radius: 50%;

}

.profile h3 {
    font-size: 2rem;
    font-weight: bold;
}

.profile p {
    font-size: 1rem;
}

.formGrid {
    display: grid;
    grid-template-columns: 48% 48%;
    grid-gap: 2%;
}

.appraisal {
    width: 100%;
    padding: 2rem 3rem;
}

.appraisal h3 {
    font-weight: bold;
    font-size: 2rem;
}

.circle {
    width: 200px;
    height: 200px;
    background: #640ad2;
    border-radius: 50%;
    padding: 4rem 2rem;
    margin: 0 auto;
    text-align: center;
    color: white;
}

.teams {
    display: grid;
    grid-template-columns: 100%;
}

.teamGrid {
    display: grid;
    grid-template-columns: 20% 80%;
}



.mteamGrid {
    display: grid;
    grid-template-columns: 23% 23% 23% 23%;
    grid-gap: 2%;
}




/* phone-only */
@media (max-width: 599px) {
    .app {
        padding: 3rem 1rem
    }

    .login {
        width: 100%;
    }

    .forms {
        padding: .5rem;
    }

    .logo img {
        width: 25%;
    }

    .mainContents {
        width: 100%;
        margin-left: 0%;
    }

    .cardGrid {
        grid-template-columns: 100%;
        grid-gap: 0;
    }

    .chartGrid {
        grid-template-columns: 100%;
    }

    .box {
        width: 100%;
        height: auto;
    }

    .profileGrid {
        grid-template-columns: 100%;
    }

    .formGrid {
        grid-template-columns: 100%;
        grid-gap: 0;
    }

}

